import React from 'react';
import moment from 'moment';

import CalendarBody from './CalendarBody.js';

class Calendar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toDay: {yyyy: '', mm: '', dd: '', ymd: '', md: ''},//  yyyy:  mmdd ymd2016-01-20md01-20
            monthEn: ['January ', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'Octorber', 'November', 'December'],
            weeksArray: [] // 
        }

    }

    componentDidMount() {
        this.getNowDate();
        this.createMonthArray();
    }

    getNowDate() { // state
        let toDay = this.state.toDay;

        let year = moment().year();
        let month = moment().month();
        let date = moment().date();

        toDay.yyyy = year;
        toDay.mm = month;
        toDay.dd = date;

        toDay.ymd = moment().format('YYYY-MM-DD');
        toDay.md = moment().format('MM-DD');
        toDay.mmEn = this.state.monthEn[month];

        this.setState({toDay: toDay});
    }

    createMonthArray(year, month) { // 
        let weekArray = []; // 
        let weeksArray = []; // 

        let _moment = new moment(); // new moment 
        let toDay = this.state.toDay;

        let _year = year ? year : toDay.yyyy;
        let _month = month ? month : toDay.mm;

        _moment.set({'year': _year, 'month': _month}); // 

        let days = _moment.daysInMonth(); // 

        let dayInfo = {}; // 

        for (let d = 1; d < days + 1; d++) { // 

            _moment.set('date', d);  // 

            let day = _moment.day(); // 

            dayInfo.yyyy = _year; // 
            dayInfo.mm = _month; // 
            dayInfo.dd = d; // 
            dayInfo.day = day; // 
            dayInfo.ymd = _moment.format('YYYY-MM-DD'); // --

            if (day === 6) { //  
                weekArray[day] = dayInfo;
                weeksArray.push(weekArray.concat());
                weekArray = [];
            } else if (d === days) { //  
                weekArray[day] = dayInfo;
                weeksArray.push(weekArray.concat());
            } else {
                weekArray[day] = dayInfo;
            }

            dayInfo = {}

        }

        for (let w in weeksArray) { // 
            for (let d = 0; d < 7; d++) {
                if (typeof(weeksArray[w][d]) === 'undefined') {
                    weeksArray[w][d] = '';
                }
            }
        }

        this.setState({weeksArray: weeksArray})

    }

    handlePreMonth() { // 
        let toDay = this.state.toDay;
        let _year = toDay.yyyy;
        let _month = toDay.mm;

        if (_month === 0) {
            _year -= 1;
            _month = 11;
        } else {
            _month -= 1;
        }

        toDay.yyyy = _year;
        toDay.mm = _month;
        toDay.mmEn = this.state.monthEn[_month];

        this.setState({toDay: toDay})
        this.createMonthArray()
    }

    handleNextMonth() { // 
        let toDay = this.state.toDay;
        let _year = toDay.yyyy;
        let _month = toDay.mm;

        if (_month === 11) {
            _year += 1;
            _month = 0;
        } else {
            _month += 1;
        }

        toDay.yyyy = _year;
        toDay.mm = _month;
        toDay.mmEn = this.state.monthEn[_month];

        this.setState({toDay: toDay})
        this.createMonthArray()
    }

    render() {
        let toDay = this.state.toDay;
        return (
            <div className="ui-calendar">
                <div className="date">
                    <table className="panel">
                        <thead>
                        <tr>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                            <th className="week-item"></th>
                        </tr>
                        </thead>
                        <CalendarBody calendarState={this.state}
                                      selectedDay={this.props.selectedDay}
                                      currentDay = {this.props.currentDay}
                                      dateArray = {this.props.dateArray}
                        />
                    </table>
                </div>
                <div className="footer">
                    <span className="pre caret-left" onClick={this.handlePreMonth.bind(this)}>
                        <svg className="icon">
                            <use xlinkHref="#icon-return"></use>
                        </svg>
                    </span>
                    <span
                        className="cur">{toDay.mmEn}{toDay.yyyy}</span>
                    <span className="next caret-right" onClick={this.handleNextMonth.bind(this)}>
                        <svg className="icon">
                            <use xlinkHref="#icon-enter"></use>
                        </svg>
                    </span>
                </div>
            </div>
        )
    }

}

export default Calendar


