import React, {Component} from 'react'
import './UserDialog.css'
import {signUp, signIn, sendPasswordResetEmail} from './leanCloud'
import ForgotPasswordForm from './ForgotPasswordForm'
import SignInOrSignUp from './SignInOrSignUp'
import UserDialogCover from './UserDialogCover'

//
export default class UserDialog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selected: 'signUp',
            selectedTab: 'signInOrSignUp',
            formData: {
                email: '',
                username: '',
                password: ''
            }
        }
    }



    signUp(e) {
        e.preventDefault()
        let {email, username, password} = this.state.formData
        let isLeagal = this.checkFormData.call(null,email,username,password)
        if(!isLeagal){
            return
        }
        let success = (user) => {
            this.props.onSignUp.call(null, user)
        }
        let error = (error) => {
            switch (error.code) {
                case 200:
                    alert('')
                case 202:
                    alert('')
                    break
                case 217:
                    alert('')
                    break
                case 125:
                    alert('@')
                default:
                    alert(error)
                    break
            }
        }
        signUp(email, username, password, success, error)
    }

    signIn(e) {
        e.preventDefault()
        let {username, password} = this.state.formData
        let success = (user) => {
            this.props.onSignUp.call(null, user)
        }
        let error = (error) => {
            switch (error.code) {
                case 210:
                    alert('')
                    break
                case 211:
                    alert('')
                    break
                case 219:
                    alert('')
                    break
                default:
                    alert(error)
                    break
            }
        }
        signIn(username, password, success, error)

    }
    //
    checkFormData(email,username,password){
        let regEmail = new RegExp("^\\w+@[\\w-]+\\.(com)$")
        let regUsername = new RegExp("\\w{3,10}")
        let regPassword = new RegExp("\\w{6,20}")
        if(!regEmail.test(email)){
            alert('@.com')
            return false
        }else if(!regUsername.test(username)){
            alert('3-10')
            return false
        }else if(!regPassword.test(password)){
            alert('6-20')
            return false
        }
        return true
    }

    changeFormData(key, e) {
        let stateCopy = JSON.parse(JSON.stringify(this.state)) //JSON
        stateCopy.formData[key] = e.target.value
        this.setState(stateCopy)
    }

    render() {

        return (
            <div className="UserDialog-Wrapper">
                <div className="UserDialog">
                    <UserDialogCover/>
                    <div className="UserPanes">
                        {this.state.selectedTab === 'signInOrSignUp' ?
                            <SignInOrSignUp
                                formData = {this.state.formData}
                                onSignIn = {this.signIn.bind(this)}
                                onSignUp = {this.signUp.bind(this)}
                                onChange = {this.changeFormData.bind(this)}
                                onForgotPassword = {this.showForgotPassword.bind(this)}
                            /> :
                            <ForgotPasswordForm
                                formData = {this.state.formData}
                                onSubmit = {this.resetPassword.bind(this)}
                                onChange = {this.changeFormData.bind(this)}
                                onSignIn = {this.returnToSignIn.bind(this)}
                            />
                        }
                    </div>
                </div>
            </div>
        )
    }

    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state)) //JSON
        stateCopy.selectedTab = 'signInOrSignUp'
        this.setState(stateCopy)
    }

    showForgotPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state)) //JSON
        stateCopy.selectedTab = 'forgotPassword'
        this.setState(stateCopy)
    }

    resetPassword(e) {
        e.preventDefault()
        sendPasswordResetEmail(this.state.formData.email)
        console.log(1)
    }
}


